import React from "react";
import "98.css"
import {BrowserRouter as Router, Link, Route} from "react-router-dom";
import Timeline from "./container/Timeline";
import Home from "./container/Home";

const timeline = () => (
    <div>
        <Timeline/>
    </div>
)
const BasicExample = () => (
    <Router basename='/react-router-github-pages-example'>
        <div>
            <div className="Home98-Container">
                {/*the window class is unique to 98.css*/}
                <div className="window">
                    <div className="title-bar">
                        <div className="title-bar-text">Welcome to the History of Computing website!</div>
                        <div className="title-bar-controls">
                        </div>
                    </div>
                    <div className="window-body">
                        <p className="Home98-content">Don't worry, the site wont always look like this. But it's a good
                            way
                            to see
                            how far technology has come. This was how Windows 98 looked! </p>
                        <ul className="tree-view">
                            <li>
                                <Link to="/timeline">timeline</Link>
                            </li>
                            <li>
                                <details open>
                                    <summary>Other things</summary>
                                    <ul>
                                        {/*the window class is unique to 98.css*/}
                                        <li>
                                            <button className="Home98-link button">
                                                <Link to="/team">Our team</Link>
                                            </button>
                                        </li>
                                        <a href="https://ecommons.luc.edu/history_facpubs/42/">
                                            <li/>
                                            <button className="Home98-link button">Book</button>
                                        </a>
                                        <a href="https://ssl.cs.luc.edu/team.html">
                                            <li/>
                                            <button className="Home98-link button">Software Systems Laboratory</button>
                                        </a>
                                        <button className="Home98-link button">
                                            <Link to="/">Click here for a normal homepage</Link>
                                        </button>
                                    </ul>
                                </details>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            {/*<ul>*/}
            {/*    <li>*/}
            {/*        <Link to="/">Home</Link>*/}
            {/*    </li>*/}
            {/*    <li>*/}
            {/*        <Link to="/timeline">timeline</Link>*/}
            {/*    </li>*/}
            {/*    <li>*/}
            {/*        <Link to="/topics">Topics</Link>*/}
            {/*    </li>*/}
            {/*</ul>*/}

            {/*<hr/>*/}

            <Route exact path="/" component={Home}/>
            <Route path="/team" component={About}/>
            {/*<Route path="/topics" component={Topics}/>*/}
            <Route path="/timeline" component={timeline}/>

        </div>
    </Router>
);


const About = () => (
    <div>
        <h2>About</h2>
    </div>
);

const Topics = ({match}) => (
    <div>
        <h2>Topics</h2>
        <ul>
            <li>
                <Link to={`${match.url}/rendering`}>Rendering with React</Link>
            </li>
            <li>
                <Link to={`${match.url}/components`}>Components</Link>
            </li>
            <li>
                <Link to={`${match.url}/props-v-state`}>Props v. State</Link>
            </li>
        </ul>

        <Route path={`${match.url}/:topicId`} component={Topic}/>
        <Route
            exact
            path={match.url}
            render={() => <h3>Please select a topic.</h3>}
        />
    </div>
);

const Topic = ({match}) => (
    <div>
        <h3>{match.params.topicId}</h3>
    </div>
);

export default BasicExample;
